h6 {
    display: inline-block;
    background: #01718f;
    color: #ffffff;
    font-size: 17px;
    font-weight: 700;
    padding: 6px 12px;
    position: relative;
    &:before {
        content: "";
        display: block;
        border-style: solid;
        border-width: 0 15px 16px;
        border-color: transparent transparent #01718f;
        position: absolute;
        bottom: 7px;
        right: 0;
        margin-right: -23px;
        z-index: 10;
        transform: rotate(90deg);
    }
}
