.button-yellow-angle {
    position: relative;
    border: 0;
    color: #000000;
    font-size: 14px;
    line-height: 40px;
    padding-right: 30px;
    padding-left: 20px;
    font-weight: 700;
    text-shadow: 0px -1px 0 rgba(255, 255, 255, 0.33);
    @include gradient3(#ffd443, #ffbf3d, #ffd443);
    &:hover {
      color: #000000;
      @include gradient3(#ffba24, #ff9c1d, #ffba24);
    }

    &:before {
      content: '';
      display: block;
      position: absolute;
      right: 0;
      top: 0;
      width: 0;
      height: 0;
      border-top: 20px solid #fff;
      border-left: 10px solid transparent;
    }
    &:after {
      content: '';
      display: block;
      position: absolute;
      right: 0;
      bottom: 0;
      width: 0;
      height: 0;
      border-bottom: 20px solid #fff;
      border-left: 10px solid transparent;
    }
    
    &._blue-bg {
        &:before {
            border-top-color: #01718F;
        }
        &:after {
            border-bottom-color: #01718F;
        }
    }
}
