* {
  box-sizing: border-box;
}
body {
  font: 14px/1.8 "Open Sans", sans-serif;
  background-color: #01718f;
}

select {
    max-width: 100%;
}

.block-introtext {
  margin: 0 0 18px;
}
.block-content {
  margin: 24px 0;
}

html {
  //background: #01718f url("../img/bg-header.jpg") no-repeat center 60px;
  background: #01718f;
}
body {
  background: url("../img/footer.jpg") no-repeat center bottom;
  background-size: 100%;
}
.wrapper {
  overflow: hidden;
  position:relative;
  z-index: 1;
}
.toolbar {
    background-color:  #005974;
    z-index: 999 !important;
    @media (max-width: $breakpoint-medium-max) {
        padding-top: 60px;
    }
    @media (max-width: $breakpoint-mini-max) {
        padding-top: 48px;
    }
}
.toolbar-mobile {
    @media (max-width: $breakpoint-medium-max) {
        position: fixed;
        right: 0;
        top: 0;
        z-index: 1010;
        width: 100%;
        background-color:  #005974;
    }
}
.header {
  position: relative;
}
.toolbar-contacts {
    position: relative;
    z-index: 20;
    float: left;
    padding: 6px 10px;
    line-height: 1.2;
    ul {
        padding: 0;
        margin: 0 -10px;
    }
    li {
        list-style: none;
        display: inline-block;
        padding: 0 10px;
        border-right: 1px solid #006584;
        &:last-child {
            border-right: 0;
        }
        a {
            color: #fff;
            font-size: 14px;
            &:hover {
                color: #b6efff;
                text-decoration: none;
            }
        }
    }
    @media (max-width: $breakpoint-mini-max) {
        padding: 6px;
        float: none;
        text-align: center;
        clear: both;
    }
}
.toolbar-contacts__title {
    font-size: 18px;
    color: #fff;
    border-bottom: 1px solid #006584;
    padding-bottom: 6px;
    margin-bottom: 6px;
    display: block;
    span {
        font-size: 70%;
    }
    &:hover {
        color: #b6efff;
        text-decoration: none;
    }
}
.toolbar-auth {
  line-height: 36px;
  text-shadow: 0px -1px 1px rgba(0, 0, 0, 0.6);
  padding: 10px 0;
  position: relative;
  z-index: 20;
  height: 60px;
  float: left;
  margin-right: 10px;
  
    @media (max-width: $breakpoint-small-max) {
        clear: both;
    }
}
.toolbar-auth__round {
    @media (max-width: $breakpoint-mini-max) {
        border-radius: 16px;
        padding: 0 6px;
    }
}
.toolbar-auth__login {
    text-indent: -9999px;
    &:before {
        content: '';
        display: block;
        background: url("../img/icon-key.png") no-repeat center center;
        width: 29px;
        height: 29px;
        margin: 4px 0;
    }
}
.toolbar-auth__link {
  color: #b6efff;
  font-size: 12px;
  text-decoration: underline;
  margin-left: 10px;
  &:hover {
    color: #ffffff;
  }
  
    @media (max-width: $breakpoint-mini-max) {
        display: none;
    }
}
.mini-cart {
  padding: 7px 10px;
  overflow: hidden;
  max-height: 100%;
  position: relative;
  z-index: 20;
  border-left: 1px solid #006584;
  border-right: 1px solid #006584;
  float: left;
  
  @media (max-width: $breakpoint-medium-max) {
    padding: 7px;
    right: 0;
    top: 0;
    position: fixed;
    z-index: 1020;
    border: 0;
  }
  @media (max-width: $breakpoint-mini-max) {
    padding: 1px 2px;
  }
}
.mini-cart__ico {
    position: relative;
    width: 46px;
    height: 46px;
    float: left;
    border: 2px solid #169bbb;
    border-radius: 50%;
    background: url("../img/icon-cart.png") no-repeat center center;
    @media (max-width: $breakpoint-large-max) {
        margin-right: 4px;
    }
    .full &,
    &:hover {
        border-color: #ffee00;
        background-image: url("../img/icon-cart-h.png");
    }
    span.ms2_total_count {
        position: absolute;
        display: inline-block;
        right: -2px;
        top: -2px;
        font-weight: bold;
        font-size: 14px;
        color: #005974;
        line-height: 1;
        text-align: center;
        border-radius: 100%;
        min-width: 18px;
        background: #ffee00;
        padding-top: 2px;
        padding-bottom: 2px;
        @media (min-width: $breakpoint-xlarge) {
            display: none;
        }
        @media (max-width: $breakpoint-large-max) {
            left: -2px;
            right: auto;
        }
    }
}
.mini-cart__info {
    float: left;
    text-shadow: 0px -1px 1px rgba(0, 0, 0, 0.6);
    margin-left: 15px;
    white-space: nowrap;
    @media (max-width: $breakpoint-large-max) {
        display: none;
    }
}
.mini-cart__lbl {
  color: #b6efff;
  font-size: 12px;
  line-height: 1;
  margin-top: 6px;
}
.mini-cart__val {
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  line-height: 1;
  margin-top: 6px;
}

