.toolbar-phones {
    display: flex;
    float: right;
    padding: 12px 0;
    box-sizing: border-box;
    justify-content: space-around;
    @media (max-width: $breakpoint-large-max) {
        padding: 14px 0;
    }
    @media (max-width: $breakpoint-medium-max) {
        padding: 14px;
    }
    @media (max-width: $breakpoint-small-max) {
        width: calc(100% - 120px);
        margin-right: 60px;
    }
    @media (max-width: $breakpoint-mini-max) {
        width: calc(100% - 100px);
        padding: 10px 2%;
        margin-right: 52px;
    }
}

.toolbar-phone {
    margin-left: 30px;
    &:first-child {
        margin-left: 0;
    }
    @media (max-width: $breakpoint-large-max) {
        margin-left: 15px;
    }
    @media (max-width: $breakpoint-small-max) {
        margin-left: 0;
    }
}

.toolbar-phone__number {
    color: #e0f4fd;
    font-size: 18px;
    font-weight: 700;
    line-height: 1;
    text-shadow: 0 -1px 1px rgba(0,0,0,.6);
    margin-bottom: 5px;
    text-align: center;
    display: block;
    text-decoration: none;
    &:hover {
        color: #e0f4fd;
        text-decoration: none;
    }
    @media (max-width: $breakpoint-large-max) {
        font-size: 16px;
    }
    @media (max-width: $breakpoint-mini-max) {
        font-size: 12px;
    }
}

.toolbar-phone__desc {
    color: #e0f4fd;
    font-size: 14px;
    line-height: 1;
    letter-spacing: 1px;
    text-shadow: 0 -1px 1px rgba(0,0,0,.6);
    text-align: center;
    @media (max-width: $breakpoint-large-max) {
        font-size: 12px;
    }
    @media (max-width: $breakpoint-mini-max) {
        font-size: 10px;
        letter-spacing: 0;
    }
}


