.message-block {
    height: 40px;
    position: relative;
    font-size: 14px;
    text-align: center;
    color: #fff;
    border-radius: 20px;
    white-space: nowrap;
    border: 1px solid #01718f;
    background: #064261;
    background: -moz-linear-gradient(top,#064261 0,#04283b 50%,#064160 100%);
    background: -webkit-gradient(linear,left top,left bottom,color-stop(0%,#064261),color-stop(50%,#04283b),color-stop(100%,#064160));
    background: -webkit-linear-gradient(top,#064261 0,#04283b 50%,#064160 100%);
    background: -o-linear-gradient(top,#064261 0,#04283b 50%,#064160 100%);
    background: -ms-linear-gradient(top,#064261 0,#04283b 50%,#064160 100%);
    background: linear-gradient(to bottom,#064261 0,#04283b 50%,#064160 100%);
    overflow: hidden;
}

.message-block__icons {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    img {
        margin: 0 5px;
        vertical-align: middle;
        position: relative;
        top: -2px;
    }
}

.message-block__front {
    position: absolute;
    z-index: 10;
    left: calc(50% - 36px);
    top: 50%;
    transform: translate(-50%, -50%);
    opacity: 1;
    transition: .3s ease;
    transition-property: top, opacity;
}

.message-block__back {
    position: absolute;
    z-index: 10;
    left: calc(50% - 36px);
    top: -50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: .3s ease;
    transition-property: top, opacity;
}

.message-block:hover .message-block__front {
    opacity: 0;
    top: 100%;
}

.message-block:hover .message-block__back {
    opacity: 1;
    top: 50%;
}

