.fixed-catalog {
    position: fixed;
    z-index: 100;
    color: #023c4e;
    font-size: 14px;
    font-weight: 700;
    line-height: 29px;
    border-radius: 6px;
    background-color: #e5b612;
    text-transform: uppercase;
    padding: 9px 15px;
    text-align: center;
    box-sizing: initial;
    left: 50%;
    bottom: 31px;
    transform: translateX(-50%);
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease;
    &_visible {
        opacity: 1;
        visibility: visible;
    }
    &:hover {
        text-decoration: none;
        background: #ffce23;
    }
    @media (min-width: $breakpoint-small) {
        display: none;
    }
}

